import React, { useState, useEffect } from 'react'
import { NavLink } from "react-router-dom";
import Card from "./UI/Card";
import { Bar } from 'react-chartjs-2';
import "./Report.css";

 export const Report = () => {

  
  
 const [reportData, setReportData] = useState([])

 useEffect(() => {
     getData()
     
 }, [])

    const getData = async () => {
        const res = await fetch("/admin/reports", {
            method: "get",
            credentials: "include",
        });
        const data = await res.json();
        setReportData(data);
        console.log(reportData)
        };

 console.log("data report: "+ reportData)
        const labels = reportData.labels
        const data = {
          labels: labels,
          datasets: [{
            label: 'Vacation Followers',
            data: reportData.values,
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)',
              'rgba(255, 159, 64, 0.2)',
              'rgba(255, 205, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(201, 203, 207, 0.2)'
            ],
            borderColor: [
              'rgb(255, 99, 132)',
              'rgb(255, 159, 64)',
              'rgb(255, 205, 86)',
              'rgb(75, 192, 192)',
              'rgb(54, 162, 235)',
              'rgb(153, 102, 255)',
              'rgb(201, 203, 207)'
            ],
            borderWidth: 1
          }]
     
        };
     
        
    
        return(
          <div className="rep">
        <Card>
        
         <div className="mainRep">
        <Bar data={data}  height='90vh'/>
        </div>
        <NavLink className="navlink" to="/">
            Back to Main
        </NavLink>
        </Card>
        </div>
        )
        
    }
    
    