import React, { useEffect, useState } from "react";
import Vacation from "./Vacation";
import { NavLink, useHistory } from "react-router-dom";
import Card from "./UI/Card";
import "./Main.css";
import {Report} from "./Report";

export default function Main() {
  let [vacationsArr, setVacationsArr] = useState([]);
  let [follows, setFollows] = useState([]);
  let [searchQuery, setSearchQuery] = useState([]);
  let user = JSON.parse(localStorage.getItem('user'))
  const history = useHistory();

  useEffect(() => {
    getVacations()
    getFollows()
  },[])

  useEffect(() => { 
    search() 
  }, [searchQuery])

  const getVacations = async () => {
    const res = await fetch("/vacations", {
      method: "get",
      credentials: "include",
    })
    const data = await res.json();
    setVacationsArr(data)
  };

  const search = async () => {
    if(!searchQuery.length) {
      getVacations();
      return
    }

    const res = await fetch(`/vacations/search/${searchQuery}`, {
      method: "get",
      credentials: "include",
    });
    const data = await res.json();
    setVacationsArr(data)
  };


  const logout = async () => {
    const res = await fetch("/users/logout", {
      method: "delete",
      credentials: "include",
    });
    const data = await res.json();
    if (data.msg) {
      localStorage.removeItem("role");
      localStorage.removeItem("username");
      history.push("/login");
    }
  };

  const getFollows = async () => {
    const res = await fetch("/follows", {
      method: "get",
      credentials: "include",
    });
    const data = await res.json();
    setFollows(data)
  };

  const sortVacations = () => {
    const followedVacationsIds = follows.map(a => {
      if(a.userID == user.id) {
        return a.vacID
      }
    })
    let sorted = vacationsArr.sort((a,b) => {
      return followedVacationsIds.indexOf(a.id) - followedVacationsIds.indexOf(b.id)
    })
    sorted.reverse()
    return sorted
  }

  return (
    <div>
      <Card className="main_header">
        <div className="main_header_items">
          <h1>
            Hello{" "}
            {localStorage.role
              ? localStorage.firstname + " " + localStorage.lastname
              : "guest! you must login to see our wiskey Vacations"}
          </h1>
          <input
          type="text"
          placeholder="Search Destination"
          onInput={(e) => setSearchQuery(e.target.value)}
        />
        
          <ul>
            {localStorage.role ? null : (
              <div>
                <li>
                  <NavLink to="/login">Login</NavLink>
                </li>
                <li>
                  <NavLink to="/register">Register</NavLink>
                </li>
              </div>
            )}
            {localStorage.role === "Admin" && (
              <button>
                <NavLink to="/add">Add New vacation</NavLink>
              </button>
              )}
               {localStorage.role === "Admin" && (
                 <button>
                <NavLink to="/report">See Report</NavLink>
              </button>
              )}
            {console.log("FFFFFFFFFFFFF",localStorage.role)}
            {localStorage.role && <button onClick={logout}>Logout</button>}
          </ul>
          
        </div>
      </Card>
      <Card className="main">
        {vacationsArr.length ? (
          sortVacations().map((vacation, i) => 
            <Vacation key={'vacation-'+i} vacation={vacation} follows={follows} getVacations={getVacations} getFollows={()=> { getFollows() }} user={user} />
          )
        ) : (
          <h1>No vacations Yet ðŸ¥´</h1>
        )}
      </Card>
      </div>
  );
}

