import React, { useEffect, useState } from 'react'

export default function Profile() {

    const [tickets, setTickets] = useState([])
    const [updateCounter, setUpdateCounter] = useState(789)
    
    useEffect(()=>{
        fetch("/users/profile",{
            credentials:"include"
        })
            .then(res=>res.json())
            .then(data=>setTickets(data))
    }, [updateCounter])


    return (
        <div>
            {
                tickets.map(t=><div>
                    <h1>{t.name}</h1>
                    <h2>{t.artist}</h2>
                    <button onClick={async ()=>{
                         const res = await fetch("/vacations/cancel/"+t.id, {
                            method: "post",
                            credentials: "include"
                        })
                        
                        const data = await res.json()
                        console.log(data)
                        setUpdateCounter(updateCounter+1)
                    }}>X</button>
                </div>)
            }
        </div>
    )
}
