import React from 'react'
import { NavLink } from "react-router-dom";
import { useHistory } from 'react-router'
import Card from "./UI/Card";
import "./Vacation.css";
import VacationDate from './VacationDate';

export default function Vacation({ vacation, follows, getFollows, user, getVacations }) {

    // const history = useHistory()

    const isFollowed = follows.find(a => {
        return a.userID == user.id && a.vacID == vacation.id
    });

    const follow = async () => {

        const res = await fetch("/vacations/follow/" + vacation.id, {
            method: "post",
            credentials: "include"
        })

        const data = await res.json()

        if (data.msg) {
            console.log(`vacation followed`)
        }

        getFollows()
    }

    const unfollow = async () => {

        const res = await fetch("/vacations/unfollow/" + isFollowed.id, {
            method: "post",
            credentials: "include"
        })

        const data = await res.json()

        if (data.msg) {
            console.log(`vacation unfollowed`)
        }

        getFollows()
    }


    const deleteVacation = async () => {
        const res = await fetch(`/vacations/${vacation.id}`, {
            method: "delete",
            credentials: "include"
        })
        getVacations()
    }
    


    return (
        <div className="vacation">
            <Card className="vacation-item">
                <div className="">
                    <h1 className="vacation-item">{vacation.title}</h1>
                    <h2 className="vacation-item">{vacation.description}</h2>
                    <h2 className="vacation-item">{vacation.destination}</h2>
                </div>
                <img className="vacation-item" src={vacation.imgUrl} alt="location Image" height="200" width="444" />
                <div className="">
                    <h2 className="vacation-item__price">${vacation.price} only!</h2>
                    <VacationDate date={vacation.startDate} />
                    <VacationDate date={vacation.endDate} />
                    {
                        localStorage.role != "Admin" && !isFollowed &&
                        <button onClick={follow}>
                            {localStorage.role === "User" ? "follow the Vacation" : "Please login"}
                        </button>

                    }
                    {
                        localStorage.role != "Admin" && isFollowed &&
                        <button onClick={unfollow}>
                            {localStorage.role === "User" ? "unfollow the Vacation" : "Please login"}
                        </button>

                    }
                    {
                        localStorage.role == "Admin" &&
                        <button onClick={deleteVacation}>X</button>
                    }
                    {
                        localStorage.role == "Admin" &&
                        <NavLink to={"/edit/"+vacation.id}>edit</NavLink>
                    }
        </div>
             </Card >
        </div >
    )
}
