import React, { useState, useEffect } from "react";
import { useHistory } from "react-router";
import { NavLink, useParams } from "react-router-dom";
import Card from "./UI/Card";
import "./Register.css";

export default function Edit() {
  const [title, setTitle] = useState("");
  const [destination, setDestination] = useState("");
  const [description, setDescription] = useState("");
  const [imgUrl, setImgUrl] = useState("");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [price, setPrice] = useState("");
  
  const history = useHistory();

  const { id } = useParams();

  useEffect(() => {
    get()
  },[])
  
  const get = async () => {
    const res = await fetch(`/vacations/${id}`, {
        method: "get",
        credentials: "include",
    })
    const data =  await res.json();
    const vacation = data[0];
    console.log(vacation);
    setTitle(vacation.title)
    setDestination(vacation.destination)
    setDescription(vacation.description)
    setImgUrl(vacation.imgUrl)
    setStartDate(vacation.startDate.split("T")[0])
    setEndDate(vacation.endDate.split("T")[0])
    setPrice(vacation.price)

    
}

  const edit = async () => {
    const res = await fetch(`/vacations/edit/${id}`, {
        method: "post",
        credentials: "include",
        body: JSON.stringify({ title, destination, description, imgUrl, startDate, endDate, price }),
        headers: { "content-type": "application/json" }

    })
    const data = res.json();
  }

  return (
    <Card className="register">
      <div className="register_items">
      <input
        type="text"
        placeholder="Title"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
      />
      <input
        type="text"
        placeholder="Destination"
        value={destination}
        onChange={(e) => setDestination(e.target.value)}
      />
      <input
        type="text"
        placeholder="Description"
        value={description}
        onChange={(e) => setDescription(e.target.value)}
      />
      <input
        type="text"
        placeholder="Image url"
        value={imgUrl}
        onChange={(e) => setImgUrl(e.target.value)}
      />
      <input
        type="date"
        placeholder="Start Date"
        value={startDate}
        onChange={(e) => setStartDate(e.target.value)}
      />
      <input
        type="date"
        placeholder="End Date"
        value={endDate}
        onChange={(e) => setEndDate(e.target.value)}
      />
      <input
        type="number"
        placeholder="Price"
        value={price}
        onChange={(e) => setPrice(e.target.value)}
      />
        <button onClick={edit}>Edit Vacation</button>
        <NavLink className="navlink" to="/">
          Back to Main
        </NavLink>
      </div>
    </Card>
  );
}
