import React from "react";
import "./VacationDate.css";

export default function VacationDate(props) {
  const date = new Date(props.date)
  const month = date.toLocaleString("en-US", { month: "long" });
  const year = date.getFullYear();
  const day = date.toLocaleString("en-US", { day: "2-digit" });
 
  return (
    <div className="vacation-date">
      <div className="vacation-date__month">{month}</div>
      <div className="vacation-date__year">{year}</div>
      <div className="vacation-date__day">{day}</div>
    </div>
  );
}
